.panel__container {
  text-align: center;
}

.allquestions {
  display: none;
}

.que {
  padding: 5px;
  background: linear-gradient(
    0deg,
    rgba(231, 231, 231, 1) 0%,
    rgba(255, 255, 255, 1) 100%
  );
  border-radius: 12px;
  min-height: 50px;
  margin-bottom: 15px;
  margin-right: auto;
  margin-left: auto;
  max-width: 810px;
  &__author {
    margin-bottom: 10px;
  }
  &__text {
    margin-bottom: 10px;
  }
  &__btn {
    background: var(--base-color);
    padding: 10px 30px;
    border-radius: 12px;
    transition: background 0.3s ease-in-out;

    &:hover {
      background: rgb(73, 198, 202);
    }

    &:active {
      transform: translateY(1.2px);
    }
  }
}

.you__que {
  margin-top: 35px;
  &-title {
    font-size: 20px;
    margin-bottom: 10px;
  }
}

.admpanel {
  display: none;
}

.panel__form {
  display: none;
}

.panel--active {
  display: block;
}

.tabs {
  margin-bottom: 15px;
  &__list {
    display: flex;
    justify-content: center;
  }

  &__btn {
    padding: 14px 90px;
    border-bottom: 2px solid var(--base-color);
    font-size: 15px;
    transition: background 0.2s ease-in-out;

    &:active {
      transform: translateY(1.2px);
    }

    &--active {
      background: var(--base-color);
    }
  }
}

.panel {
  &__data {
    margin: 10px 0 25px;
    display: flex;
    justify-content: center;

    & p:not(:last-child) {
      margin-right: 5px;
    }
  }

  &__form {
  }

  &__rep {
    background: rgba(0, 0, 0, 0.12);
    display: flex;
    margin: 0 auto;
    padding: 7px;
    width: 509px;
    border-radius: 12px;
    & input {
      display: block;
    }
  }

  &__btn {
    margin-top: 5px;
    background: rgb(80, 215, 219);
    width: 509px;
    height: 33px;
    border-radius: 12px;
    transition: background 0.3s ease;

    &:hover {
      background: rgb(71, 191, 196);
    }

    &:active {
      transform: translateY(1.2px);
    }
  }
}

.your-nick {
  color: rgb(45, 167, 172);
}

.your-status {
  color: red;
}

.space {
  margin-right: 5px;
}

