.bigred {
  font-size: 20px;
  color: red;
}

body {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
}

@font-face {
  font-family: "Jos";
  src: url("../fonts/Jost-Bold.woff2") format("woff2"), url("../Jost-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Jos";
  src: url("../fonts/Jost-Regular.woff2") format("woff2"), url("../Jost-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Jos";
  src: url("../fonts/Jost-SemiBold.woff2") format("woff2"), url("../Jost-SemiBold.woff") format("woff");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
.header {
  padding-top: 50px;
}
.header__container {
  text-align: center;
}

.buttons {
  text-align: center;
}

.auth__btn {
  margin: 20px 0 20px;
  background: rgb(80, 215, 219);
  padding: 12px 42px;
  border-radius: 12px;
  font-size: 15px;
  transition: background 0.3s ease;
}
.auth__btn:hover {
  background: rgb(71, 191, 196);
}
.auth__btn:active {
  transform: translateY(1.2px);
}

.error {
  margin-top: 5px;
  font-size: 13px;
  color: red;
}

.hidden {
  display: none;
}

.modal {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 9999;
  text-align: center;
  padding-top: 200px;
  width: 100%;
  background: rgba(112, 112, 112, 0.3);
}
.modal__window {
  position: relative;
  padding: 30px 0;
  background: white;
  width: 330px;
  margin: 0 auto;
  border-radius: 15px;
}
.modal__close {
  position: absolute;
  top: 18px;
  right: 25px;
  background: none;
}
.modal__close-svg {
  width: 20px;
  height: 20px;
}
.modal__forms {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 20px;
}
.modal__forms input {
  display: block;
  width: 250px;
  border-radius: 7px;
  padding: 10px 10px;
}
.modal__forms-nickname {
  margin-bottom: 7px;
  background: rgb(177, 177, 177);
}
.modal__forms-password {
  background: rgb(177, 177, 177);
}

.panel__container {
  text-align: center;
}

.allquestions {
  display: none;
}

.que {
  padding: 5px;
  background: linear-gradient(0deg, rgb(231, 231, 231) 0%, rgb(255, 255, 255) 100%);
  border-radius: 12px;
  min-height: 50px;
  margin-bottom: 15px;
  margin-right: auto;
  margin-left: auto;
  max-width: 810px;
}
.que__author {
  margin-bottom: 10px;
}
.que__text {
  margin-bottom: 10px;
}
.que__btn {
  background: var(--base-color);
  padding: 10px 30px;
  border-radius: 12px;
  transition: background 0.3s ease-in-out;
}
.que__btn:hover {
  background: rgb(73, 198, 202);
}
.que__btn:active {
  transform: translateY(1.2px);
}

.you__que {
  margin-top: 35px;
}
.you__que-title {
  font-size: 20px;
  margin-bottom: 10px;
}

.admpanel {
  display: none;
}

.panel__form {
  display: none;
}

.panel--active {
  display: block;
}

.tabs {
  margin-bottom: 15px;
}
.tabs__list {
  display: flex;
  justify-content: center;
}
.tabs__btn {
  padding: 14px 90px;
  border-bottom: 2px solid var(--base-color);
  font-size: 15px;
  transition: background 0.2s ease-in-out;
}
.tabs__btn:active {
  transform: translateY(1.2px);
}
.tabs__btn--active {
  background: var(--base-color);
}

.panel__data {
  margin: 10px 0 25px;
  display: flex;
  justify-content: center;
}
.panel__data p:not(:last-child) {
  margin-right: 5px;
}
.panel__rep {
  background: rgba(0, 0, 0, 0.12);
  display: flex;
  margin: 0 auto;
  padding: 7px;
  width: 509px;
  border-radius: 12px;
}
.panel__rep input {
  display: block;
}
.panel__btn {
  margin-top: 5px;
  background: rgb(80, 215, 219);
  width: 509px;
  height: 33px;
  border-radius: 12px;
  transition: background 0.3s ease;
}
.panel__btn:hover {
  background: rgb(71, 191, 196);
}
.panel__btn:active {
  transform: translateY(1.2px);
}

.your-nick {
  color: rgb(45, 167, 172);
}

.your-status {
  color: red;
}

.space {
  margin-right: 5px;
}

.layout {
  display: grid;
  height: 100%;
  width: 100%;
  overflow: hidden;
  grid-template-rows: 50px 1fr;
  grid-template-columns: 1fr 1fr 1fr;
}

input[type=radio] {
  display: none;
}

label.nav {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-bottom: 2px solid #8e44ad;
  background: #ecf0f1;
  user-select: none;
  transition: background 0.4s, padding-left 0.2s;
  padding-left: 0;
}
input[type=radio]:checked + .page + label.nav {
  background: #9b59b6;
  color: #ffffff;
  padding-left: 20px;
}
input[type=radio]:checked + .page + label.nav span {
  padding-left: 20px;
}
input[type=radio]:checked + .page + label.nav svg {
  opacity: 1;
}
label.nav span {
  padding-left: 0px;
  position: relative;
}
label.nav svg {
  left: 0;
  top: -3px;
  position: absolute;
  width: 15px;
  opacity: 0;
  transition: opacity 0.2s;
}

.page {
  grid-column-start: 1;
  grid-row-start: 2;
  grid-column-end: span 3;
  padding: 0px 20px;
  display: flex;
  align-items: center;
}

.page-contents > * {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.2s, transform 0.2s;
}
.page-contents > *:nth-child(1) {
  transition-delay: 0.4s;
}
.page-contents > *:nth-child(2) {
  transition-delay: 0.6s;
}
.page-contents > *:nth-child(3) {
  transition-delay: 0.8s;
}
.page-contents > *:nth-child(4) {
  transition-delay: 1s;
}
.page-contents > *:nth-child(5) {
  transition-delay: 1.2s;
}
.page-contents > *:nth-child(6) {
  transition-delay: 1.4s;
}
.page-contents > *:nth-child(7) {
  transition-delay: 1.6s;
}
.page-contents > *:nth-child(8) {
  transition-delay: 1.8s;
}
.page-contents > *:nth-child(9) {
  transition-delay: 2s;
}
.page-contents > *:nth-child(10) {
  transition-delay: 2.2s;
}
.page-contents > *:nth-child(11) {
  transition-delay: 2.4s;
}
.page-contents > *:nth-child(12) {
  transition-delay: 2.6s;
}
.page-contents > *:nth-child(13) {
  transition-delay: 2.8s;
}
.page-contents > *:nth-child(14) {
  transition-delay: 3s;
}
.page-contents > *:nth-child(15) {
  transition-delay: 3.2s;
}
.page-contents > *:nth-child(16) {
  transition-delay: 3.4s;
}
.page-contents > *:nth-child(17) {
  transition-delay: 3.6s;
}
.page-contents > *:nth-child(18) {
  transition-delay: 3.8s;
}
.page-contents > *:nth-child(19) {
  transition-delay: 4s;
}
.page-contents > *:nth-child(20) {
  transition-delay: 4.2s;
}

input[type=radio] + .page {
  transition: transform 0.2s;
  transform: translateX(100%);
}

input[type=radio]:checked + .page {
  transform: translateX(0%);
}
input[type=radio]:checked + .page .page-contents > * {
  opacity: 1;
  transform: translateY(0px);
}

.page-contents {
  max-width: 100%;
  width: 500px;
  margin: 0 auto;
}

@media (max-width: 530px) {
  .panel__rep {
    width: 100%;
  }
  .panel__btn {
    width: 100%;
  }
}