.buttons {
  text-align: center;
}

.auth {
  &__btn {
    margin: 20px 0 20px;
    background: rgb(80, 215, 219);
    padding: 12px 42px;
    border-radius: 12px;
    font-size: 15px;
    transition: background 0.3s ease;

    &:hover {
      background: rgb(71, 191, 196);
    }

    &:active {
      transform: translateY(1.2px);
    }
  }
}

.error {
  margin-top: 5px;
  font-size: 13px;
  color: red;
}

.hidden {
  display: none;
}

.modal {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 9999;
  text-align: center;
  padding-top: 200px;
  width: 100%;
  background: rgba(112, 112, 112, 0.3);
  &__window {
    position: relative;
    padding: 30px 0;
    background: white;
    width: 330px;
    margin: 0 auto;
    border-radius: 15px;
  }

  &__close {
    position: absolute;
    top: 18px;
    right: 25px;
    background: none;
    &-svg {
      width: 20px;
      height: 20px;
    }
  }

  &__title {
  }

  &__descr {
  }

  &__forms {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 20px;

    & input {
      display: block;
      width: 250px;
      border-radius: 7px;
      padding: 10px 10px;
    }
  }

  &__forms-nickname {
    margin-bottom: 7px;
    background: rgb(177, 177, 177);
  }

  &__forms-password {
    background: rgb(177, 177, 177);
  }
}
